.uc__nav-slider {
  margin-bottom: 30px; }

.uc__filter {
  position: relative;
  margin-bottom: 20px;
  padding: 10px 20px;
  border-radius: 10px;
  background-color: #F6F6F6;
  overflow: hidden; }
  .uc__filter--expanded {
    max-height: unset !important; }
  .uc__filter--about {
    padding: 0;
    background-color: #fff;
    margin-bottom: 30px; }

.uc__filter-title {
  margin-bottom: 7px;
  font-size: 14px;
  line-height: 17px;
  font-weight: 700; }

.uc__filter-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px; }
  .uc__filter--about .uc__filter-list {
    padding: 4px; }

.uc__filter-link {
  display: block;
  background-color: #fff;
  padding: 4px 10px;
  border-radius: 6px;
  font-size: 14px;
  line-height: 17px;
  white-space: nowrap;
  transition: background-color 0.2s ease, color 0.2s ease; }
  .uc__filter-link--active {
    background-color: #E7E7F7;
    color: #001684; }
  .uc__filter--about .uc__filter-link {
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
    display: block;
    padding: 8px 12px;
    font-size: 14px;
    line-height: 17px;
    border-radius: 6px; }

.uc__filter-btn-box {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  height: 100px;
  padding-bottom: 20px;
  background: linear-gradient(to top, #fff, transparent); }
  .uc__filter--expanded .uc__filter-btn-box {
    display: none; }

.uc__slide-content {
  font-size: 18px;
  line-height: 22px; }
  .uc__slide-content ul {
    padding-left: 10px;
    list-style-position: inside;
    list-style-type: disc; }

.uc__btn-more {
  width: 100%; }

.uc__rating {
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
  margin-bottom: 30px;
  padding: 20px 30px;
  border-radius: 10px; }

.uc__rating-box {
  display: flex;
  justify-content: space-between;
  gap: 30px; }

.uc__rating-text {
  width: 100%;
  max-width: 215px; }

.uc__rating-value {
  margin-bottom: 6px;
  font-size: 18px;
  line-height: 30px;
  text-align: center; }
  .uc__rating-value strong {
    font-size: 24px;
    font-weight: 700; }

.uc__rating-total {
  margin-bottom: 35px;
  font-size: 14px;
  line-height: 17px;
  color: #929292;
  text-align: center; }

.uc__rating-btn {
  width: 100%; }

.uc__rating-data {
  width: 100%;
  max-width: 510px; }

.uc__rating-row {
  display: flex;
  align-items: center;
  gap: 10px; }
  .uc__rating-row:not(:last-child) {
    margin-bottom: 6px; }

.uc__rating-stars {
  display: flex;
  flex-shrink: 0; }

.uc__rating-star {
  width: 20px;
  height: 20px; }

.uc__rating-progress {
  position: relative;
  flex-grow: 1;
  height: 4px;
  border-radius: 4px;
  background-color: #F6F6F6;
  overflow: hidden; }

.uc__rating-progress-val {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 4px;
  border-bottom: 4px solid #FFCD0E; }

.uc__rating-counter {
  display: block;
  flex-shrink: 0;
  width: 40px;
  font-size: 16px;
  line-height: 20px;
  text-align: center; }

.uc__sort-row {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px; }
